// import 'materialize-css';

export function configure(aurelia) {
  aurelia.use
    .standardConfiguration()
    .developmentLogging()
    .plugin('aurelia-materialize-bridge', b => b.useAll())

    .globalResources('shared/logger');

  if (window.Waves) {
    window.Waves.displayEffect = function() {};
  }

  return aurelia.start().then(aurelia => aurelia.setRoot('app'));
}
